import React, { Component } from 'react';
import SpatialNavigation from '../spatial_navigation';
import axios from 'axios';

require('./Css/Dashboard.css');

const KeyCode = {
    BACK: '461',
    BACK_LOCAL: 8
}
const dashboardNav = [{
    buttonText: 'Plumbing',
    id: 'plumbing'
},
{
    buttonText: 'Bedding',
    id: 'bedding'
},
{
    buttonText: 'Bath Amenities',
    id: 'bathamenities'
}];
const plumbingNav = [{
    buttonText: 'Toilet Stopped Up',
    id: 'toiletstoppedup'
},
{
    buttonText: 'Toilet Not Flushing',
    id: 'toiletnotflushing'
},
{
    buttonText: 'Toilet Running',
    id: 'toiletrunning'
},
{
    buttonText: 'Toilet Overflowing',
    id: 'toiletoverflowing'
}]
class Dashboard extends Component {

    constructor(props) {
        super(props);
        this.state = {
            isPlumbing: false,
            keycode: 0,
            showComment: false,
            plumbingDesc: '',
            responseMsg: '',
            showresponseMsg: false,
            backKeyMsg:""
        }
    }
    componentDidMount() {
        document.addEventListener('keydown', this.keyPressEvent);
        SpatialNavigation.init();
        SpatialNavigation.add({
            selector: '.focusable'
        });
        SpatialNavigation.makeFocusable();
        SpatialNavigation.focus();
    }

    keyPressEvent = (event) => {
        var keyCode = event.keyCode || event.which;
        
        switch (keyCode) {
            case keyCode.BACK:
            case KeyCode.BACK_LOCAL:
                this.setState({
                    keycode: keyCode,
                    plumbingDesc: '',
                    showComment: false,
                    isPlumbing: false,
                    plumbing:false,
                    backKeyMsg:"Back Key Pressed"
                }, () => {
                    SpatialNavigation.focus('#navwrapper .focusable', true);
                })
                break;
            default:
                break;
        }

        this.setState({
            keycode: keyCode
        });
    }

    backToDashboard = () => {
        this.setState({
            plumbingDesc: '',
            showComment: false,
            isPlumbing: false
        }, () => {
            SpatialNavigation.focus('#navwrapper .focusable', true);
        })
    }

    clickDashboard = (e) => {
        const pageName = e.target.id;
        this.setState({
            [pageName]: true
        }, () => {
            SpatialNavigation.focus('#navwrapper .focusable', true);
        })
    }

    handleChange = (e) => {
        let { name, value } = e.target;
        this.setState({
            [name]: value
        })
    }
    clickSublink = (e) => {
        const pageName = e.target.id;
        if (pageName === 'toiletstoppedup') {
            this.setState({
                showComment: true
            })
        }
    }
    handleSubmit = () => {
        const data = this.state;
        const payload = {
            plumbing: {
                toiletstoppedup: data.plumbingDesc
            }
        }
        const url = 'https://feedback-39e94.firebaseio.com/servicerequest.json';
        axios.post(url, JSON.stringify(payload))
            .then((response) => {
                if (response.status === 200) {
                    this.setState({
                        plumbingDesc: '',
                        showComment: false,
                        plumbing: false,
                        responseMsg: 'Your Case has been created',
                        showresponseMsg: true
                    }, () => {
                        SpatialNavigation.focus('#navwrapper .focusable', true);
                        setTimeout(() => {
                            this.setState({
                                responseMsg: '',
                                showresponseMsg: false
                            })
                        }, 2000)
                    });
                }
            })
    }
    render() {
        const { responseMsg, showresponseMsg } = this.state;
        console.log(this.state.plumbing)
        return (
            <div className="mainContainer">
                <div className="backkey">1{this.state.keycode}</div>
                {this.state.backKeyMsg && <div className="backkey">{this.state.backKeyMsg}</div>}
                {showresponseMsg && <div className="success">{responseMsg} <button className="close focusable"> &#x2716; </button></div>}
                {!this.state.plumbing && <h1>Associate: Request Service</h1>}
                {this.state.plumbing && <h1>Plumbing</h1>}
                <div className="block-wrapper" id="navwrapper">
                    {
                        !this.state.plumbing && dashboardNav && dashboardNav.map(item => {
                            return <button key={item.id} className="focusable" id={item.id} onClick={(e) => this.clickDashboard(e)}>{item.buttonText}</button>
                        })
                    }

                    {
                        this.state.plumbing && plumbingNav.map(item => {
                            return <button key={item.id} className="focusable" id={item.id} onClick={(e) => this.clickSublink(e)}>{item.buttonText}</button>
                        })
                    }
                </div>
                {
                    this.state.showComment && <div className="formRow">
                        <label className="label">Comments?</label>
                        <textarea rows={6} className="focusable" id={'plumbing'} name="plumbingDesc" value={this.state.plumbingDesc} onChange={this.handleChange}></textarea>
                        <div className="buttonRow">
                            <button className=" btn submit focusable" onClick={this.handleSubmit}>Submit</button>
                        </div>
                    </div>
                }
            </div>
        )
    }
}

export default Dashboard;